import React from 'react'
import styled from 'styled-components'
import { GatsbyImage } from 'gatsby-plugin-image'

import 'semantic-ui-css/components/icon.min.css'
import '../../styles/animations.css'

const StoryTitle = styled.h1`
	z-index: 999;
	display: inline;
	background: rgba(255, 255, 255, 1);
	padding: 1rem 1.5rem;
	border-radius: 1px;

	@media screen and (max-width: 500px) {
		margin: 0 2rem 15rem 2rem;
	}
`

const FullHeightImage = styled.div`
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100vw;
	height: calc(100svh - 80px);
	height: calc((var(--vh, 1vh) * 100) - 80px);

	@media screen and (max-width: 500px) {
		align-items: flex-end;
		height: calc(100svh - 70px);
		height: calc((var(--vh, 1vh) * 100) - 70px);
	}
`

const ScrollAnimation = styled.div`
	position: absolute;
	bottom: 20rem;
	color: white;

	@media screen and (max-width: 500px) {
		bottom: 18rem;
	}
`

const HeaderComponent = (props) => {
	return (
		<FullHeightImage>
			<GatsbyImage
				alt={'An image.'}
				image={props.image}
				style={{
					height: '100%',
					width: '100%',
					position: 'absolute',
					top: 0,
					left: 0,
				}}
				imgStyle={{ objectFit: 'cover' }}
			/>
			{props.title && props.title !== '' && <StoryTitle>{props.title}</StoryTitle>}
			{props.scrollAnimation && (
				<ScrollAnimation className='bounce'>
					<i className='icon big chevron down' />
				</ScrollAnimation>
			)}
		</FullHeightImage>
	)
}

export default HeaderComponent
